"use client";
import React, { useState, useEffect, useRef, useCallback, useMemo } from 'react';
import { Share2, ThumbsUp, MessageCircle, Send, PlayCircle, Search, Video } from 'lucide-react';
import Image from "next/image";

// --- MOCK DATA GENERATOR ---
const generateHighlights = (page, count) => {
    // Expanded categories list for mock data
    const categories = ['Football', 'Boxing', 'Volleyball', 'Tennis', 'Basketball', 'Cricket', 'Rugby', 'Formula 1'];

    return Array.from({ length: count }).map((_, index) => ({
        id: `page-${page}-item-${index}`,
        title: `Manchester City Clinches Dramatic Champions League Victory ${index + 1}`,
        thumbnail: `https://placehold.co/600x340/4c3b71/ffffff?text=Highlight+${page}-${index + 1}`,
        views: Math.floor(Math.random() * 5000) + 500,
        date: `${Math.floor(Math.random() * 24)}d ago`,
        duration: "10:24",
        category: categories[Math.floor(Math.random() * categories.length)]
    }));
};

// --- COMPONENTS ---

// Combined Header with Search (Replaces SectionHeader & FilterBar)
const HeaderWithSearch = ({ title, searchQuery, onSearchChange }) => (
    <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 mb-6">
        {/* Left: Title Pill */}
        <div className="bg-[#4c3b71] text-white px-4 py-1.5 rounded-md text-sm font-bold flex items-center gap-2 h-10">
            <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M14.752 11.168l-3.197-2.132A1 1 0 0010 9.87v4.263a1 1 0 001.555.832l3.197-2.132a1 1 0 000-1.664z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
            </svg>
            HIGHLIGHTS
        </div>

        {/* Right: Search Bar */}
        <div className="relative w-full sm:w-72">
            <input
                type="text"
                placeholder="Search highlights..."
                value={searchQuery}
                onChange={(e) => onSearchChange(e.target.value)}
                className="w-full pl-10 pr-4 py-2.5 bg-white border border-gray-200 rounded-lg text-sm focus:outline-none focus:ring-2 focus:ring-[#4c3b71]/20 focus:border-[#4c3b71] transition-all shadow-sm"
            />
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 text-gray-400" size={18} />
        </div>
    </div>
);

const HighlightCard = ({ item }) => (
    <div className="group bg-white rounded-xl overflow-hidden shadow-sm hover:shadow-lg transition-all duration-300 border border-gray-100 flex flex-col h-full cursor-pointer">
        {/* Thumbnail */}
        <div className="relative w-full aspect-video flex-shrink-0 overflow-hidden">
            <Image src={item.thumbnail} alt={item.title} className="w-full h-full object-cover transition-transform duration-700 group-hover:scale-105" />
            <div className="absolute inset-0 bg-black/20 group-hover:bg-black/10 flex items-center justify-center transition-all opacity-0 group-hover:opacity-100">
                <PlayCircle className="text-white drop-shadow-lg scale-90 group-hover:scale-100 transition-transform" size={48} />
            </div>
            <span className="absolute bottom-2 right-2 bg-black/70 text-white text-[10px] font-bold px-2 py-0.5 rounded backdrop-blur-sm">
        {item.duration}
      </span>
            {/* Category Badge */}
            <span className="absolute bottom-2 left-2 bg-gray-100 text-gray-800 text-[10px] font-bold px-2 py-0.5 rounded shadow-sm uppercase tracking-wide">
        {item.category}
      </span>
        </div>

        {/* Content */}
        <div className="p-4 flex flex-col flex-grow justify-between">
            <div>
                <h3 className="text-gray-900 font-bold text-sm sm:text-base line-clamp-2 leading-snug group-hover:text-[#4c3b71] transition-colors mb-2">
                    {item.title}
                </h3>
                <p className="text-xs text-gray-500 line-clamp-2 mb-3">
                    An unforgettable night as the team comes from behind to secure a win in the finals.
                </p>
            </div>

            <div className="flex items-center justify-end text-xs text-gray-400">
                <span>{item.date}</span>
            </div>
        </div>
    </div>
);

